.sidebar {
  position: absolute;
  z-index: 200;
  left: 0;
  top: 55px;
  display: -ms-flexbox;
  display: flex;
}

.tabs {
  border-radius: 2px;
}

.tabButton {
  cursor: pointer;
  background: rgba(255, 255, 255, 0.3);
  padding: 10px;
  height: 46px;
  width: 46px;
  text-align: center;
  font-size: 1.5em;
}

.tabButton:hover {
  background: rgba(255, 255, 255, 0.9);
}

.content {
  background: rgba(255, 255, 255, 0.8);
  max-height: calc(100vh - 200px);
  overflow-y: scroll;
  -webkit-box-shadow: 1px 2px 2px rgba(0, 0, 0, 0.2);
          box-shadow: 1px 2px 2px rgba(0, 0, 0, 0.2);
}

.contentInner {
  padding: 0 20px;
}



